@import url(https://fonts.googleapis.com/css2?family=Rampart+One&family=Poiret+One&family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{display:flex;flex-direction:column;justify-content:center;align-items:center;min-height:100%;background-color:#ff6347}.App{text-align:center}.App #title{font-family:"Press Start 2P", cursive;color:#06720f;text-shadow:2px 2px 0 #fff;font-size:1.2em;margin-top:30px}.App #container{display:flex;align-items:flex-start}.App #container svg{background-color:#ff6347;border-radius:5px;box-shadow:rgba(0,0,0,0.2) 0px 60px 40px -7px}.App #container svg #description{font-family:"Press Start 2P", cursive;fill:#fff;font-size:0.9em;text-shadow:2px 2px 0 #06720f}.App #container svg #canvas{min-height:600px;min-width:1200px}.App #container svg .cell:hover{stroke-width:2;stroke:#fff}.App #container svg line{stroke:#fff}.App #container svg path{stroke:#fff}.App #container svg text{fill:#fff;font-weight:650}.App #container #name{display:flex;flex-direction:column}.App #container #name #legend{margin-left:20px}.App #container #name #legend text{font-size:12px}.App #container #name #da3ker{color:white;font-size:1.2em;letter-spacing:2px;font-family:"Rampart One", cursive;margin-top:60px;-webkit-transform:rotate(90deg);transform:rotate(90deg)}.App #tooltip{visibility:hidden;width:auto;height:auto;position:absolute;background-color:rgba(71,255,99,0.8);padding:20px;text-align:center;font-size:0.8em;font-weight:500;border-radius:10px}

